:root {
  --font-primary: "Manrope", sans-serif;
}

:root {
  --color-bg-body: hsl(210, 46%, 95%);
  --color-primary: hsl(217, 19%, 35%);
  --color-text: hsl(214, 17%, 51%);
  --color-icons: hsl(212, 23%, 69%);
  --color-blink: hsl(30, 61%, 45%);
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  font-size: 0.8125rem;
  background-color: var(--color-bg-body);
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}
@media (max-width: 21.875em) {
  body {
    padding: 3rem 0;
  }
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
}

.container {
  width: 90vw;
  max-width: 68.75rem;
}

.card {
  width: 90vw;
  max-width: 21.0625rem;
  border-radius: 0.75rem;
  overflow: hidden;
  box-shadow: 0px 40px 40px -10px rgba(201, 213, 225, 0.503415);
}
@media (min-width: 48em) {
  .card {
    display: flex;
    max-width: 44.375rem;
    overflow: unset;
  }
}
.card__image {
  display: block;
  background: url(../images/drawers.jpg) no-repeat;
  background-size: cover;
  height: 12.5rem;
}
@media (min-width: 48em) {
  .card__image {
    flex: 1;
    height: auto;
    border-radius: 0.5rem 0 0 0.5rem;
  }
}
.card__content {
  background-color: white;
}
@media (min-width: 48em) {
  .card__content {
    flex: 1.5;
    border-radius: 0 0.5rem 0.5rem 0;
  }
}
.card__copy {
  padding: 2rem 2rem 1rem;
}
.card__title {
  color: var(--color-primary);
  font-size: 1rem;
  letter-spacing: 0.01875rem;
  margin-bottom: 1.375rem;
}
@media (min-width: 48em) {
  .card__title {
    font-size: 1.25rem;
  }
}
.card__title::after {
  content: "|";
  color: var(--color-blink);
  margin-left: 0.1875rem;
  opacity: 1;
  animation: blink 0.8s infinite;
}
@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
.card__description {
  color: var(--color-text);
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0.025rem;
}
@media (min-width: 48em) {
  .card__description {
    letter-spacing: normal;
  }
}
.card__footer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
}
@media (min-width: 48em) {
  .card__footer {
    padding: 0 2rem 2rem;
  }
}
.card__info-personal {
  display: flex;
  align-items: center;
  justify-content: initial;
  gap: 1rem;
}
.card__avatar {
  width: 2.5rem;
  border-radius: 100%;
}
.card__author {
  color: var(--color-primary);
  font-size: 0.8125rem;
  line-height: 1.7;
}
.card__time {
  font-weight: 500;
  color: var(--color-icons);
}
.card__btn {
  background: transparent;
  border: none;
  z-index: 10;
  cursor: pointer;
}
.card__btn i {
  background-color: var(--color-bg-body);
  color: var(--color-text);
  font-size: 1.125rem;
  padding: 0.375rem;
  border-radius: 50%;
  transition: all ease 0.5s;
}
.card__btn i:hover {
  background-color: var(--color-text);
  color: var(--color-bg-body);
}
.card__btn i.active {
  background-color: var(--color-text);
  color: var(--color-bg-body);
}
.card__share {
  background-color: var(--color-primary);
  display: flex;
  align-items: center;
  justify-content: initial;
  gap: 1rem;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  padding: 0 2.125rem;
  visibility: hidden;
  opacity: 0;
  transition: all ease 0.7s;
}
@media (min-width: 48em) {
  .card__share {
    flex-direction: column;
    gap: 0.5rem;
    top: -5.625rem;
    right: -1.5625rem;
    bottom: initial;
    width: 10.625rem;
    padding: 0.5rem 0;
    border-radius: 0.5rem;
    box-shadow: 0px 40px 40px -10px rgba(201, 213, 225, 0.503415);
  }
  .card__share::before {
    content: "";
    background-color: var(--color-primary);
    position: absolute;
    bottom: -0.5rem;
    left: 50%;
    width: 1.25rem;
    height: 1.25rem;
    transform: rotate(45deg);
  }
}
@media (min-width: 64em) {
  .card__share {
    flex-direction: row;
    gap: 1rem;
    top: -5rem;
    right: -4.0625rem;
    width: 15rem;
    padding: 0.75rem 1.5rem;
  }
}
.card__share.show {
  visibility: visible;
  opacity: 1;
}
.card__share span {
  color: var(--color-icons);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__social-medias {
  display: flex;
  align-items: center;
  justify-content: initial;
  gap: 0.5rem;
}
@media (min-width: 48em) {
  .card__social-medias {
    gap: 1rem;
  }
}
.card__social-medias a {
  display: inline-block;
}
.card__social-medias a i {
  font-size: 1.625rem;
  color: white;
  transition: all ease 0.5s;
}
.card__social-medias a i:hover {
  color: var(--color-icons);
}