@use '../util' as *;

html {
    font-size: 100%;
    scroll-behavior: smooth;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: var(--font-primary);
    font-size: rem(13);
    background-color: var(--color-bg-body);

    @include flex(center);
    min-height: 100vh;

    @include breakpoint-down(small) {
        padding: rem(48) 0;
    }
}
